import { Component, OnInit } from '@angular/core';
import {ClientbriefService} from '../shared/clientbrief.service';
import {Router} from '@angular/router';

@Component({
  moduleId: module.id,
  selector: 'app-clientbrief',
  templateUrl: 'clientbrief.component.html',
  styleUrls: ['clientbrief.component.css'],
  providers:[ClientbriefService]
})
export class ClientbriefComponent implements OnInit {
  
  private clientBriefs:any[];

  constructor(private _clientBrief:ClientbriefService,private _router:Router) {}

  ngOnInit() {
    console.log("clientbrief component loaded");
    this._clientBrief.loadClientBriefs().subscribe(
      data => this.clientBriefs = data,
      error => console.log(error),
      () => console.log("completed client briefs loading")
    );
  }
  
  public createMediaPlan(client:string) {
    this._router.navigate(['/mediaplan',{'target':client}]);
  }

}
